import React, { useEffect, useState } from 'react';
import Close from './Close';

function Cartmenu() {
  const [count, setCount] = useState(1)

  function plus() {
    setCount(count +1)
  }

  function minus() {
      setCount(count -1)
  }

  // useEffect(() => {
  //   if (count == 1) {
  //     document.getElementById('minus').style.display = "none";
  //   } else {
  //     document.getElementById('minus').style.display = "inline-block";}
  // })

    return(
        <div className="cartmenu">
          <Close />
          <div className="carthead">
            <ul>
              <li>Food</li>
              <li>Qty</li>
              <li>Price</li>
              <li>Total</li>
            </ul>
            <hr />
          </div>
          <div className="ullist">
            {/* <ul className="cover">
              <li></li>
              <li><span id="minus" onClick={minus}>-</span> {count} <span id="plus" onClick={plus}>+</span></li>
          
              
            </ul> */}
          </div>
          
          <div id="total">
            
            <h3>Total</h3>
            <h3>--</h3>
          </div>
        </div>
    )
}


export default Cartmenu